@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  transition: 200ms color ease-in;
}

::-webkit-scrollbar {
  display: none;
}

.dashboard {
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  height: 100vh;
  min-height: 500px;
  padding-bottom: 50px;
}

.main__wrapper {
  -webkit-flex: 0.7 1;
          flex: 0.7 1;
  overflow: scroll;
  background-color: #121212;
}

@media only screen and (max-width: 1023px) {
  .main__wrapper {
    padding-bottom: 50px;
  }
}

@media only screen and (max-width: 768px) {
  .main__wrapper {
    -webkit-flex: 0.85 1;
            flex: 0.85 1;
  }
}

@media only screen and (min-width: 1800px) {
  .dashboard {
    padding-left: 7%;
    background-color: #040404;
  }
}

.sidebar::-webkit-scrollbar {
  display: none;
}

.sidebar {
  -webkit-flex: 0.3 1;
          flex: 0.3 1;
  background-color: #040404;
  color: white;
  overflow: scroll;
  position: relative;
}

.sidebar__header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  padding: 10px;
  background-color: #040404;
  padding-bottom: 10px;
}

.sidebar__logos {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  margin-bottom: 10px;
}

.sidebar__logo {
  width: 50%;
  margin-bottom: 20px;
  margin: auto;
}

.sidebar__title {
  margin-top: 20px;
  margin-left: 10px;
  border-bottom: 1px white solid;
}

@media only screen and (max-width: 768px) {
  .sidebar {
    -webkit-flex: 0.15 1;
            flex: 0.15 1;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .mobile.sidebar {
    -webkit-flex: 100vw 1;
            flex: 100vw 1;
    display: block;
  }

  .sidebar:hover {
    cursor: pointer;
  }

  .sidebar:hover .sidebar__burger {
    background-color: rgba(46, 45, 45, 0.404);
    color: white;
    cursor: pointer;
  }

  .sidebar:focus .sidebar__burger,
  .sidebar__burger:focus {
    box-shadow: 0 4px 40px white;
    outline: none;
  }
}

.sidebarItem {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: grey;
  height: 40px;
  cursor: pointer;
  padding: 10px;
}

.sidebarItem:hover :not(:last-child) {
  color: white;
}

.sidebarItem:focus {
  color: white;
  text-shadow: 0 4px 40px white;
  outline: none;
}

.sidebarItem__icon {
  margin-right: 5px;
  margin-left: -10px;
}

.sidebarItem-active :not(:last-child) {
  color: white;
}

.sidebarItem__text {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin-right: 10px;
}

.sidebarItem__text:hover {
  cursor: pointer;
  color: white;
}

.sidebarItem__title {
  font-size: 20px;
}

.sidebarItem__favorite {
  margin-left: auto;
}

.sidebarItem__favorite.sidebarItem-favorite {
  color: #1ed760;
}

.sidebarItem__favorite:hover {
  color: white;
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

.sidebarItem__favorite:focus {
  color: white;
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  box-shadow: 0 4px 40px white;
  border-radius: 90%;
  outline: none;
}

.sidebarItem-favorite:hover {
  color: #1ed760;
}

.sidebarItem-favorite:focus {
  color: #1ed760;
  box-shadow: 0 4px 40px #1ed760;
}

.main {
  color: white;
}

.main::-webkit-scrollbar {
  display: none;
}

.main__info {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  height: 20vh;
  min-height: 150px;
  padding: 10px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: linear-gradient(rgb(91, 87, 115), rgb(26, 25, 25));
  box-shadow: 0 4px 60px black;
  z-index: 1;
}

.main__logo {
  height: 100%;
  box-shadow: 0 4px 60px black;
}

.main__infoText {
  margin-left: 20px;
}

.main__name {
  font-size: 30px;
}

.main__description {
  font-size: 12px;
  font-weight: 300;
}

.main__songs {
  padding: 10px;
  background: linear-gradient(rgb(54, 52, 65), rgb(26, 25, 25));
}

@media only screen and (max-width: 424px) {
  .main__info {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    width: 100%;
  }

  .main__infoText {
    margin-left: 20px;
    width: 90%;
  }

  .main__name {
    width: 90%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .main__logo {
    height: 75%;
  }
}

.songRow {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  margin: 5px 0;
  padding: 5px;
}

.songRow:hover {
  cursor: pointer;
  background-color: black;
  opacity: 0.8;
  box-shadow: 0 4px 60px black;
}

.songRow:focus {
  opacity: 0.8;
  background-color: black;
  box-shadow: 0 4px 60px black;
  outline: none;
}

.songRow__img {
  height: 50px;
}

.songRow__info {
  margin-left: 10px;
}

.songRow__name {
  font-size: 16px;
}

.songRow__artist {
  font-size: 12px;
  font-weight: 400;
}

.songRow-active {
  background-color: rgba(0, 0, 0, 0.514);
}

.songRow__icon {
  margin-right: 5px;
}

.gallery {
  padding-top: 50px;
  color: #b3b3b3;
  overflow: scroll;
  height: 96vh;
}

.gallery__items {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

@media only screen and (min-width: 1440px) {
  .gallery {
    padding-right: 15%;
  }
}

.galleryItem {
  width: 30%;
  min-width: 250px;
  margin: 10px;
  background-color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  padding: 10px;
  border-radius: 5%;
  box-shadow: 0 4px 60px black;
  transition: 200ms ease-in;
}

.galleryItem:hover {
  background-color: rgba(46, 45, 45, 0.404);
  color: white;
  cursor: pointer;
}

.galleryItem:focus {
  background-color: rgba(46, 45, 45, 0.404);
  color: white;
  box-shadow: 0 4px 40px white;
  outline: none;
}

.galleryItem__img {
  width: 90%;
  border-radius: 10%;
  margin: 10px 0;
}

.galleryItem__name {
  font-size: 24px;
  font-weight: 700;
  width: 90%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.galleryItem__description {
  font-size: 12px;
  font-weight: 400;
  width: 90%;
  overflow: hidden;
  text-overflow: ellipsis;
}

.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 2;
}

.header:hover .header__logout,
.header__wrapper:focus .header__logout {
  display: block !important;
  margin-right: 10px;
  margin-left: auto;
  cursor: pointer;
  z-index: 2;
}

.header__wrapper:focus,
.header__logout:focus {
  box-shadow: 0 4px 40px white;
  outline: none;
}

.header__wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  position: absolute;
  right: 10px;
  top: 10px;
  background: black;
  color: white;
  border-radius: 40px;
  box-shadow: 0 4px 60px black;
  width: 120px;
  height: 44px;
  padding: 2px;
}

.header__text {
  font-size: 12px;
  font-weight: 800px;
  margin: 5px;
  margin-left: 10px;
  overflow: hidden;
  width: 60px;
}

.header__logout {
  display: none !important;
}

.footer {
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #282828;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  color: #ffffff;
}

.admin {
  background-color: black;
  min-height: 100vh;
  color: white;
}

.admin__button {
  padding: 10px;
  background-color: black;
  color: grey;
  border: none;
  font-size: 16px;
  cursor: pointer;
  margin-bottom: 20px;
  transition: 200ms ease-in;
}

.admin__button:hover {
  -webkit-transform: translateX(10px) scale(1.2);
  transform: translateX(10px) scale(1.2);
  color: white;
  font-weight: bold;
}

.admin__button:focus {
  -webkit-transform: translateX(10px) scale(1.2);
  transform: translateX(10px) scale(1.2);
  text-shadow: 0 4px 40px white;
  color: white;
  font-weight: bold;
  outline: none;
}

.admin__header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.admin__instructions {
  padding: 10px;
}

.admin__help {
  color: gold;
  font-size: 18px;
  text-align: end;
  font-family: "Dancing Script", cursive;
}

.printData {
  padding: 10px;
}

.printData__header {
  font-weight: bold;
  font-size: 20px;
}

.printData__element {
  padding: 10px;
  background-color: grey;
  display: inline-block;
  margin: 15px 20px;
  border-radius: 10px;
  transition: 200ms ease-in;
  cursor: pointer;
  font-size: 16px;
  text-transform: uppercase;
}

.printData__element:hover {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

.printData__element:focus {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  box-shadow: 0 4px 40px white;
  outline: none;
}

.printData__body {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.printData__add {
  cursor: pointer;
  transition: 200ms ease-in;
}

.printData__add:hover {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

.printData__add:focus {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  box-shadow: 0 4px 40px white;
  border-radius: 50%;
  outline: none;
}

.printData__element.selected {
  background-color: white;
  color: black;
}

.printData__button {
  background-color: black;
  color: white;
  border: none;
  cursor: pointer;
}

.printData__button:hover {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  -webkit-transform: translateX(10px) scale(1.2);
  transform: translateX(10px) scale(1.2);
  text-shadow: 0 4px 40px white;
}

.printData__button:focus {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  text-shadow: 0 4px 40px white;
  outline: none;
}

.form {
  margin: 10px 10px 0 10px;
  padding: 10px;
}

.form__input,
.form__button {
  padding: 10px;
  background-color: lightgray;
  border: 1px black solid;
  font-size: 20px;
}

.form__button {
  font-weight: bold;
  cursor: pointer;
}

.form__button:hover,
.form__input:hover {
  background-color: white;
}

.form__button:focus,
.form__input:focus {
  box-shadow: 0 4px 40px white;
  outline: none;
}

.form__error {
  color: red;
}

.festivals {
  background-color: black;
  min-height: 100vh;
}

.carousel {
  height: 70vh;
  width: 100%;
  min-height: 350px;
  margin: auto;
  background: black;
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.9)),
    url(/festival-radio/static/media/back.850c7ec9.jpg);
  background-position: center;
  background-size: cover;
}

.CarouselItem {
  height: 70vh;
  min-height: 350px;
}

.carousel__block {
  height: 60vh;
  min-height: 300px;
  width: 90%;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
}

.carousel__image {
  max-width: 80%;
  height: 80%;
  object-fit: scale-down;
  margin-top: auto;
  padding: 20px;
}

.carousel__text {
  font-weight: bold;
  text-transform: uppercase;
}

.carousel__button {
  font-size: 30px;
  padding: 10px 15px;
  background-color: white;
  border: none;
  border-radius: 10px;
  transition: 200ms ease-in;
  cursor: pointer;
}

.carousel__block:hover .carousel__button {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

.carousel__button:focus {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  box-shadow: 0 4px 40px white;
  outline: none;
}

.festivals__list {
  margin: 20px 20px 0 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.festivals__login {
  color: white;
  position: absolute;
  top: 0;
  right: 0;
  padding: 10px 30px;
  text-decoration: none;
  transition: 200ms ease-in;
  z-index: 1;
  border: none;
  cursor: pointer;
}

.festivals__login:hover {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  font-weight: bold;
}

.festivals__login:focus {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  text-shadow: 0 4px 40px white;
  outline: none;
}

.festivalBlock {
  background-color: #121212;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  border-radius: 10px;
  width: 300px;
  height: 100px;
  margin: 20px 20px;
  transition: 200ms ease-in;
  cursor: pointer;
  box-shadow: 0 4px 40px grey;
}

.festivalBlock__logo {
  width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.festivalBlock:hover {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  box-shadow: 0 4px 40px grey;
}

.festivalBlock:focus {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
  box-shadow: 0 4px 40px white;
  outline: none;
}

