.festivals {
  background-color: black;
  min-height: 100vh;
}

.carousel {
  height: 70vh;
  width: 100%;
  min-height: 350px;
  margin: auto;
  background: black;
  background-image: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(rgba(0, 0, 0, 0.7)),
      to(rgba(0, 0, 0, 0.9))
    ),
    url(../../resources/img/back.jpg);
  background-image: -o-linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.9)),
    url(../../resources/img/back.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.9)),
    url(../../resources/img/back.jpg);
  background-position: center;
  background-size: cover;
}

.CarouselItem {
  height: 70vh;
  min-height: 350px;
}

.carousel__block {
  height: 60vh;
  min-height: 300px;
  width: 90%;
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  cursor: pointer;
}

.carousel__image {
  max-width: 80%;
  height: 80%;
  -o-object-fit: scale-down;
  object-fit: scale-down;
  margin-top: auto;
  padding: 20px;
}

.carousel__text {
  font-weight: bold;
  text-transform: uppercase;
}

.carousel__button {
  font-size: 30px;
  padding: 10px 15px;
  background-color: white;
  border: none;
  border-radius: 10px;
  -webkit-transition: 200ms ease-in;
  -o-transition: 200ms ease-in;
  transition: 200ms ease-in;
  cursor: pointer;
}

.carousel__block:hover .carousel__button {
  -webkit-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2);
}

.carousel__button:focus {
  -webkit-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2);
  -webkit-box-shadow: 0 4px 40px white;
  box-shadow: 0 4px 40px white;
  outline: none;
}

.festivals__list {
  margin: 20px 20px 0 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: space-evenly;
  -ms-flex-pack: space-evenly;
  justify-content: space-evenly;
}

.festivals__login {
  color: white;
  position: absolute;
  top: 0;
  right: 0;
  padding: 10px 30px;
  text-decoration: none;
  -webkit-transition: 200ms ease-in;
  -o-transition: 200ms ease-in;
  transition: 200ms ease-in;
  z-index: 1;
  border: none;
  cursor: pointer;
}

.festivals__login:hover {
  -webkit-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2);
  font-weight: bold;
}

.festivals__login:focus {
  -webkit-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2);
  text-shadow: 0 4px 40px white;
  outline: none;
}
